.wrapper_content {
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;

  & .content {
    & .myplan {
      display: flex;
      align-items: center;
      flex-direction: column;
      color: #ffffff;

      h1 {
        font-size: 41px;
        font-weight: 500;
        letter-spacing: 4px;
        padding: 39px 0 41px 0;
      }

      & .line {
        width: 960px;
        height: 1px;
        background: linear-gradient(180deg,rgba(255,255,255,0) 0%,rgba(255,255,255,1) 50%,rgba(255,255,255,0) 100%);
      }

      h2 {
        font-size: 30px;
        font-weight: 300;
        padding: 23px 0 13px 0;
      }

      p {
        font-size: 14px;
        font-weight: 500;
        color: rgba(219, 183, 137, 1);
        padding: 10px 0;
      }

      & .progress {
        width: 270px;
        height: 19px;
        background: rgba(29, 32, 113, 1);
        border-radius: 10px;
        align-items: flex-start;

        & .used {
          width: 76%;
          height: 19px;
          background: linear-gradient(90deg, rgba(239, 220, 190, 1) 0%, rgba(219, 183, 137, 1) 100%);
          border-radius: 10px;
        }
      }
      & .upgrade{
        padding: 10px 30px;
        background: linear-gradient(180deg,rgba(89,97,206,1) 0%,rgba(45,50,160,1) 100%);
        border-radius: 4px;
        &:hover{ 
          cursor: pointer;
          box-shadow: 1px 1px 1px 1px rgba(0,0,0,.3);
        }
        margin-bottom: 41px;
      }
    }
  }
}