.header {
  width: 100%;
  height: 85px;
  background-color: rgba(45, 50, 160, 1);
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  z-index: 10;

  & .content {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #ffffff;

    .logo {
      background: url('../../assets/images/LOGO1@2x.png') center/100% no-repeat;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 145px;
      height: 33px;
      margin-left: 10px
    }

    .header_web {
      height: 100%;
      font-size: 12px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-weight: 300;
      letter-spacing: 1px;
      padding: 0 10px;
      
      &:hover {
        background-color: rgba(0, 0, 0, .1);
        cursor: pointer;
      }
    }

    .header_menu {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      height: 100%;
      position: relative;
    }
  }

  border-bottom: 1px solid rgba(255, 255, 255, .2);
}