.sol_wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;

  .sol_inner {
    color: rgba(255, 255, 255, 1);
    width: 342px;
    background: linear-gradient(180deg, rgba(72, 80, 204, 1) 0%, rgba(45, 50, 160, 1) 100%);
    border-radius: 4px;

    .sol_top {
      display: flex;
      justify-content: center;
      padding: 20px 0;
      border-bottom: 1px solid rgba(255, 255, 255, .19);

      .sol_top_img {
        width: 72px;
        height: 70px;
        background: url('../../assets/images/LOGO1_small_icon@2x.png') center/100% no-repeat;
      }

      .sol_top_title {
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 20px;
        font-weight: 500;
        letter-spacing: 1px;
      }
    }

    .sol_bottom {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 20px 0;
      border-top: 1px solid rgba(255, 255, 255, .19);

      .sol_bottom_title {
        font-size: 12px;
        font-weight: 300;
        letter-spacing: 1px;
        padding: 0 10px;
      }

      .sol_bottom_img {
        padding: 0 10px;
        width: 40px;
        height: 20px;
      }
    }
  }
}