.watermark {
  width: 556px;

  video {
    margin: 94px 0 20px 0;
    width: 556px;
    height: 313px;
    box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.5);
    border-radius: 4px;
    border: 2px solid rgba(255, 255, 255, 1);
    object-fit: contain;
  }

  .watermark_option {
    .watermark_box {
      display: flex;
      justify-content: space-around;
      .watermark_left{}
      .watermark_right{}

      .watermark_button {
        background: rgba(39, 22, 126, 1);
        box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.5);
        border-radius: 4px;
        width: 158px;
        text-align: center;
        padding: 7px 0;
        &:hover{cursor: pointer;}
      }
    }
  }
}