@keyframes deleteList {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.4);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes downloadList {
  0% {
    transform: translate(0, -0px);
  }

  50% {
    transform: translate(0, -5px);
  }

  100% {
    transform: translate(0, -0px);
  }
}

.download_list_download {
  width: 14px;
  height: 15px;
  position: absolute;
  right: 10px;
  top: 10px;
  background: url('../../../assets/images/download_icon@2x.png') center/100% no-repeat;

  &:hover {
    cursor: pointer;
    transform: translate(0, -5px);
    animation: 1s downloadList infinite;
  }
}

.download_list_line {
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  .progress_bar {
    width: 100%;
    background: linear-gradient(270deg, rgba(54, 60, 162, 1) 0%, rgba(47, 52, 146, 1) 100%);
    border-radius: 4px;
    .progress_bar_inner{
      height: 2px;
      width: 70%;
      background-color: #ffffff;
    }
  }
}


.download_list_progress {
  position: absolute;
  right: 10px;
  top: 10px;
  font-size: 10px;
  font-weight: 300;
}

.download_list_delete {
  width: 10px;
  height: 10px;
  position: absolute;
  right: -5px;
  top: -5px;
  background: url('../../../assets/images/close_icon@2x.png') center/100% no-repeat;

  &:hover {
    cursor: pointer;
    animation: 1s deleteList infinite;
  }
}

.download_list_outoptions {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  padding-bottom: 8px;

  .download_list_outoption {
    width: 77%;
    padding-right: 8px;
    display: flex;
    flex-direction: column;

    .download_list_outoption_box {
      width: 100%;
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      padding-top: 8px;

      .outoption_customize {
        display: flex;
        justify-content: center;
        align-items: center;
        background: rgba(0, 0, 0, 0.01);
        border-radius: 2px;
        border: 1px solid rgba(255, 255, 255, .5);
        font-size: 14px;
        font-weight: 500;
        margin-right: 5px;

        &:hover {
          cursor: pointer;
          background: rgba(255, 255, 255, .1);
        }
      }
      .active {
        background: rgba(0, 0, 0, 0.2);
        border: 1px solid rgba(255, 255, 255, 1);
      }

      input {
        width: 80%;
        outline: none;
        display: flex;
        justify-content: center;
        align-items: center;
        background: rgba(0, 0, 0, 0.15);
        border-radius: 2px;
        border: 1px solid rgba(255, 255, 255, 1);
        font-size: 14px;
        font-weight: 500;
        text-align: center;
        color: #ffffff;
        margin-left: 2px;
      }

      label {
        width: 25%;
        text-align: center;
      }
    }

    .outoption_topbox {
      div {
        width: 64px;
        height: 24px;
      }
    }

    .outoption_bottombox {
      width: 100%;
      div {
        width: 90px;
        height: 24px;
        display: flex;
        justify-content: center;
        align-items: center;
      }
      .outoption_bottombox_input{
        width: 60%;
      }
    }
  }

  .start_outoption {
    width: 23%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 20px;
    background-color: rgba(65, 73, 197, 1);
    box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.5);
    border-radius: 10px;
    font-size: 16px;
    font-weight: 500;

    &:hover {
      cursor: pointer;
      background: rgba(0, 0, 0, .1);
    }
  }
}